.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body{
  background-color: grey;
}

/*header*/
header{
  padding: 20px 35px;
  background-color: #fff;
}

header h1 {
  font-style: oblique;
}

header a {
  color: black;
  text-decoration: none;
}

header nav {
  margin: 10px 7px;
  position: absolute; top: 25px; right: 0;
}

header nav ul li a {
  margin: 0 25px;
  font-weight: lighter;
  font-size: larger;
}

header li {
  list-style-type: none;
  display: inline;
}

/* about me*/
.portrait {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 40px;
}

.about-me {
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  text-align: justify;
}

.about-me {
  color: #fff;
  font-weight: lighter;
}

.topic-style {
  padding: 20px;
  max-width: 600px;
  margin-right: auto;
  margin-left: auto;
  font-size: 30px;
  color: black;
  text-align: center;
  background-color: #fff;
}

/*portfolio*/
.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  
}

footer {
  padding: 20px 35px;
  background-color: #fff
}

/*contact*/
.contact-section{
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  text-align: justify;
  color: #fff;
  font-weight: lighter;
}

.email-box {
  text-align: center;
}